body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: plum;
}

.appTitle {
    text-align: center;
    font-size: 40px;
    
}

.appSubtitle {
    text-align: center;
    font-size: 30px;   
}

.appList {
    display: flex;
    text-align: center;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    list-style: none;
    padding: 0px;
}

.appElement{
    font-size: 25px;
    padding: 10px;
    text-decoration: none;
    color: black;
}
.Calculator {
    font-size: 35px;
    text-align: center;
    background-color: #cccccc;
    height:410px;
    width:290px;
    margin: auto;
    margin-top: 8%;
    padding: 3px;
    border-radius:10px;
    box-shadow:5px 10px 30px 5px #444;
}

.CalculatorName {
    font-family: 'Orbitron', sans-serif;
    margin-top: 20px;
}

.CalculatorBrand {
    font-family: 'Orbitron', sans-serif;
    font-size: 18px;
    margin-top: 10px;
}

.homeButton {
    width: auto;
    background-color: transparent;
    border-color:darkmagenta;
    border-radius: 25px;
    color: darkmagenta;
    box-shadow:none;
    font-size: 14px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin-left: 50px;
    }

.ButtonStyle {
    font-family: 'Orbitron', sans-serif;
    text-align:center;
    font-size:.7em;
    color:#fff;
    display:inline block;
    position: relative;
    margin: 2px;
    border-radius:10px;
    background:#333;
    box-shadow: 0px 3px 0px 0px #202020;
    width:60px;
    height:50px;

}
.Display {
    font-size: 28px;
    background:#6B9C83;
    border:solid;
    border-width:1px;
    border-color:#888;
    font-family: 'Orbitron', sans-serif;
    margin: 7% -3% 4% 5%;
    text-align:right;
    border-radius:10px;
    width:260px
}

.littleDisplay {
    font-size: 15px;
}
.inputTodoLabel {
    display: none;
}
.inputTodoInput {
    width: 350px;
    height: 25px;
    margin: auto;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-style: italic;
    font-size: 14px;
}
.done {
    text-decoration: line-through;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.notdone{
    text-decoration: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.TodoListList{
    display: flex;
    flex-direction: column-reverse;
}
.inactive {
    color: black;
    text-decoration: none;
    padding: 6px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.isActive{
    color: darkmagenta;
    font-weight: 700;
    padding: 6px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-decoration: none;
}

.filterPanelMenu {
    display: flex;
    flex-direction: row;
    margin: auto;
}

.buttonClear{
    width: auto;
    background-color: transparent;
    border-color:darkmagenta;
    border-radius: 25px;
    color: darkmagenta;
    box-shadow:none;
    font-size: 14px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.Background {
  position:absolute;
  width: 100vw;
  height: 100vh;
  background-image: url(/static/media/modalbackground.9aecd722.png);
  margin:0px;
  top: 50%; 
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); 
}

.Foreground {
  position: relative;
  -webkit-transform: translate(50%, 50%);
          transform: translate(50%, 50%); 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 50%;
    margin: 0;
    padding: 35px;
    background-color: white;
    border-radius: 30px;
    box-shadow: 0 0 2.5rem rgba(0, 0, 0, 0.5);
    opacity: 1;
}

.ExitButton {
  background-color: transparent;
  border: none;
  font-size: 30px;
  font-weight: bold;
  margin-left: 95%;
}

.ModalMessage {
  font-size: 25px;
  font-weight: bold;
}

.ModalButton_Yes {
  background-color: #2ecc71;
  font-size: 20px;
  border-radius: 5px;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 40px 10px;

}

.ModalButton_No {
  background-color: #e74c3c;
  font-size: 20px;
  border-radius: 5px;
  color: white;
  border: none;
  padding: 10px 20px;
}
